import React from "react";
import Datatraphong from "./Datatraphong";
import "./Traphong.scss";

const TableTraPhong = () => {
  return (
    <div>
      <table className="table_traphong">
        <tr>
          <th>M Phng </th>
          <th>Tn Khch Hng </th>
          <th>S in Thoi </th>
          <th>S CCCD </th>
          <th>Ngy n</th>
          <th>Ngy Ri i</th>
          <th>Phng S </th>
          <th>S Tin Cn thanh ton </th>
          <th>Chc Nng </th>
        </tr>
        {Datatraphong.map((item) => (
          <tr>
            <td>{item.Maphong}</td>
            <td>{item.name}</td>
            <td>{item.phone}</td>
            <td>{item.CCCD}</td>
            <td>{item.dayorder}</td>
            <td>{item.day_of_leaving}</td>
            <td>{item.nameroom}</td>
            <td>{item.thanhtoan}</td>
            <td>
                  <button>
                    <i class="fa-solid fa-pen-to-square"></i>{" "}
                    <span>Chnh Sa</span>
                  </button>
                  <button>
                    <i class="bx bxs-trash"></i> <span>Xa</span>
                  </button>
                </td>
          </tr>
        ))}
      </table>
    </div>
  );
};

export default TableTraPhong;
