import React, { useState } from "react";
import Navbar from "../../components/Navbar/Navbar";
import Stack from "@mui/material/Stack";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import "./QlLuong.scss";
import { dataQLluong } from "./data";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Qlluong = () => {
  const [search, setSearch] = useState("");
  const [selectedItem, setSelectedItem] = useState({});
  const [data, setData] = useState(dataQLluong);
  const [open, setOpen] = useState(false);
  const [story, setStory] = useState("");
  const [mesage, setMesage] = useState("");

  var box = document.querySelector(".qlluong-modal");
  var update = document.querySelector(".btn-update");

  const clickbox = () => {
    box.style.display = "block";
  };

  React.useEffect(() => {
    setData(dataQLluong);
  }, []);

  const handleClose = () => {
    box.style.display = "none";
  };

  // const handleSearch = () => {
  //   setSearch("");
  //   if (dataQLluong.find((item) => item.mnv === search)) {
  //     box.style.display = "block";
  //     setData(dataQLluong.find((item) => item.mnv === search));
  //   } else {
  //     setMesage("Khng tm thy nhn vin!!!");
  //     setStory("error");
  //     handleClick();
  //   }
  // };

  const handleSearchEmployees = () => {
    const newArr = dataQLluong.filter((item) =>
      item?.name?.toLowerCase()?.includes(search.toLowerCase())
    );
    setData(newArr);
  };

  const handleFillSearchEmployees = (val) => {
    setSearch(val);
    if (val === "") {
      setData(dataQLluong);
    }
  };

  const handleClick = () => {
    setOpen(true);
  };

  const handleCloseDelete = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };

  const handleClickDelete = () => {
    handleClick();
    setMesage("Delete successfully!!!");
    setStory("success");
  };

  const handleClickUpdateClose = () => {
    update.style.display = "none";
  };

  const handleClickUpdate = () => {
    update.style.display = "block";
  };

  const hamdleCloseUpdate = () => {
    handleClick();
    setMesage("Update successfully!!!");
    setStory("success");
    handleClose();
  };

  const handleDeleteEmployee = (e) => {
    handleClickDelete();
    if (dataQLluong.find((object) => object.mnv === e.mnv)) {
      const deleteItem = dataQLluong.filter((item) => item.mnv !== e.mnv);
      setData(deleteItem);
      console.log(deleteItem);
    }
    handleClick();
    setMesage("Delete successfully!!!");
    setStory("success");
  };

  return (
    <div>
      <Navbar />

      <div className="qlluong-container">
        <div className="qlluong-header">
          <h3 className="qlluong-title">Qun L Lng</h3>
          <div className="qlluong-search">
            <input
              value={search}
              // onChange={(e) => {
              //   setSearch(e.target.value);
              // }}
              onChange={(e) => {
                handleFillSearchEmployees(e.target.value);
              }}
              type="text"
              className="qlluong-input"
              placeholder="Nhp tn nhn vin cn tm kim ......"
            />
            <i
              class="fa-solid fa-magnifying-glass qlluong-icon"
              onClick={handleSearchEmployees}
            ></i>
          </div>
        </div>

        <div className="qlluong-table">
          <table>
            <tr>
              <th>M nhn vin</th>
              <th>Tn nhn vin</th>
              <th>S ngy</th>
              <th>Ngy ngh</th>
              <th>Ngy cng</th>
              <th>Thnh tin</th>
              <th className="chucnang">Chc nng</th>
            </tr>
            {data.map((item) => (
              <tr>
                <td>{item.mnv}</td>
                <td>{item.name}</td>
                <td>{item.daywork}</td>
                <td>{item.dayoff}</td>
                <td>{item.day}</td>
                <td>{item.price}</td>
                <td className="function-table-qlluong">
                  <button
                    onClick={() => {
                      setSelectedItem(item);
                      clickbox();
                      handleClickUpdateClose();
                    }}
                  >
                    View
                  </button>
                  <button
                    onClick={() => {
                      setSelectedItem(item);
                      clickbox();
                      handleClickUpdate();
                    }}
                  >
                    Update
                  </button>
                  <button
                    onClick={() => {
                      handleDeleteEmployee(item);
                    }}
                  >
                    Delete
                  </button>
                </td>
              </tr>
              // <QlluongTable dataTable={item} />
            ))}
          </table>
        </div>

        <div className="qlluong-modal">
          <div className="qlluong-modal-child">
            <div className="qlluong-child-close" onClick={handleClose}>
              <i class="fa-solid fa-xmark "></i>
            </div>
            <div className="qlluong-content-title">
              <h2>Thng tin nhn vin</h2>
            </div>

            <div className="qlluong-content">
              <div className="qlluong-content-img">
                <img src={selectedItem.image} alt="" />
              </div>

              <div className="qlluong-content-information">
                <div className="qlluong-content-detail">
                  <p>M nhn vin: {selectedItem.mnv}</p>
                  <p>Tn nhn vin: {selectedItem.name}</p>
                  <p>S ngy: {selectedItem.daywork}</p>
                  <p>Ngy ngh: {selectedItem.dayoff}</p>
                  <p>Ngy cng: {selectedItem.day}</p>
                  <p>Thnh tin: {selectedItem.price}</p>
                </div>

                <button className="btn-update" onClick={hamdleCloseUpdate}>
                  Update
                </button>
              </div>
            </div>
          </div>
        </div>

        <Stack spacing={10} sx={{ width: "100%" }}>
          <Snackbar
            open={open}
            autoHideDuration={1000}
            onClose={handleCloseDelete}
          >
            <Alert
              onClose={handleCloseDelete}
              severity={story}
              sx={{ width: "100%" }}
            >
              {mesage}
            </Alert>
          </Snackbar>
        </Stack>
      </div>
    </div>
  );
};

export default Qlluong;
