import React from "react";
import "./TableDatPhong.scss";
import Datadatphong from "./Datadatphong";

const TableDatPhong = () => {
  return (
    <div>
      <table className="table_datphong">
        <tr>
          <th>M t Phng  </th>
          <th>Tn Khch Hng </th>
          <th>S in Thoi </th>
          <th>S CCCD </th>
          <th>Ngy t</th>
          <th>Tn Phng</th>
          <th>S Ngi </th>
          <th>Tr Trc</th>
          <th>ghi ch</th>
          <th>Chc nng</th>


        </tr>
        {Datadatphong.map((item) => (
          <tr>
            <td>{item.Maphong}</td>
            <td>{item.name}</td>
            <td>{item.phone}</td>
            <td>{item.CCCD}</td>
            <td>{item.dayorder}</td>
            <td>{item.nameroom}</td>
            <td>{item.songuoio}</td>
            <td>{item.thanhtoan}</td>
            <td>{item.note}</td>
            <td><button>
                    <i class="fa-solid fa-pen-to-square"></i>{" "}
                    <span>Chnh Sa</span>
                  </button>
                  <button>
                    <i class="bx bxs-trash"></i> <span>Xa</span>
                  </button></td>


          </tr>
        ))}
      </table>
    </div>
  );
};

export default TableDatPhong;
